	    "term": "Demisexual",
	    "definition": "Demisexual is where someone doesnt feel sexual attraction until there is an emotional bond. For me personally, I may think someone looks nice/beautiful/attractive (I call that aesthetic attraction), or I may want to start a romantic relationship with them (romantic attraction), but I dont want to see them naked or have sex with them. Its not like a light switch for me either, like I suddenly want to do everything sexual with them all at once. Its more gradual; I usually want to kiss someone before seeing them naked. In my own experience, it hasnt been any emotional bond (ie friendship), but it has to be romantic in nature. I started knowing I was demisexual when my friends and I were discussing which celebrity we would sleep with. I said someone I admire and think is pretty, but I definitely wouldnt want to sleep with them. ",
