	    "timestamp": "2018-03-02T20:56:24.951Z",
	    "term": "Genderqueer",
	    "definition": "Similar to the weird queer, genderqueer is a sort of catch all term that means a gender that is out of the ordinary. Some folks (like me) like to identify with trans along with this label, others non binary, and some just simply genderqueer. To me it means that you dont fit into a binary but you may not be outside it either; youre a mix in your own way, similar to how if you look up the definitions of the colors of the genderqueer flag youll get some beautiful results about the chartreuse, white, and lavender. ",
